.advantages {
  font-style: normal;
  text-transform: uppercase;
}
.advantages__wrapper {
  margin-bottom: 38px;
}
.advantages__list {
  margin-block-start: 0;
  margin-block-end: 0;
  padding: 0;
}
.advantages__item {
  list-style-type: none;
}
.advantages__item--blue {
  width: 100%;
  color: #ffffff;
  background-color: #81b3d2;
}
.advantages__item--grey {
  background-color: #eeeeee;
}
.advantages__content {
  text-align: center;
  margin: 0 auto;
}
.advantages__content--blue {
  margin: 0 20px;
}
.advantages__content--grey {
  margin: 0 35px;
  border-bottom: #d6d6d6 solid 1px;
}
.advantages__item--grey:last-child .advantages__content--grey {
  border: none;
}
.advantages__title {
  margin: 0;
  padding-top: 24px;
  padding-bottom: 11px;
}
.advantages__number {
  margin: 0;
  padding-bottom: 17px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.advantages__description {
  margin: 0;
  padding-bottom: 26px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.advantages__image img {
  display: block;
  background-color: #81b3d2;
  object-fit: cover;
  width: 100%;
}
@media (max-width: 767px) {
  .advantages__item--blue:nth-child(1) .advantages__title {
    padding-top: 49px;
  }
  .advantages__item--blue:nth-child(1) .advantages__description {
    padding-bottom: 40px;
  }
  .advantages__item--blue:nth-child(2) {
    display: flex;
    flex-direction: column;
  }
  .advantages__item--blue:nth-child(2) .advantages__title {
    padding-top: 40px;
  }
  .advantages__item--blue:nth-child(2) .advantages__description {
    padding-bottom: 35px;
  }
  .advantages__item--blue:nth-child(2) .advantages__image {
    order: 1;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .advantages__item--blue {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column: 1/4;
  }
  .advantages__content {
    grid-column: 1/2;
  }
  .advantages__content--blue {
    margin: 0 36px;
  }
  .advantages__content--grey {
    border: none;
  }
  .advantages__title {
    padding-top: 41px;
  }
  .advantages__number {
    padding-bottom: 23px;
  }
  .advantages__image {
    height: 256px;
    grid-column: 2/4;
  }
  .advantages__item--blue:nth-child(1) {
    background-image: none;
  }
  .advantages__item--blue:nth-child(2) .advantages__content {
    grid-column: 3/4;
  }
  .advantages__item--blue:nth-child(2) .advantages__image {
    grid-column: 1/3;
  }
  .advantages__item--grey {
    min-height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantages__title {
    padding: 49px 90px 11px;
  }
  .advantages__description {
    padding: 0 35px;
  }
}
