}
.review-form__title {
  text-align: center;
  padding-bottom: 22px;
}
.review-form__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.review-form__item {
  position: relative;
  display: grid;
  grid-template-columns: 16% 84%;
  min-height: 46px;
  margin-bottom: 16px;
}
.review-form__label {
  padding-left: 16px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
}
.review-form__label--choice {
  grid-column: 2/3;
  display: flex;
  align-items: center;
  text-transform: none;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.review-form__label:hover {
  cursor: pointer;
}
/* ИНПУТЫ */
.review-form__input {
  width: calc(100% - 20px);
  height: 58px;
  padding: 0;
  padding-left: 16px;
  margin: 0;
  margin-bottom: 16px;
  border: none;
  background-color: #f2f2f2;
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.review-form__input:hover {
  background-color: #e5e5e5;
}
.review-form__input:focus,
.review-form__input:active {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
}
.review-form__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
}
.review-form__section--impression .review-form__mark {
  border-radius: 50%;
}
.review-form__choice:checked + .review-form__mark {
  position: absolute;
  width: 46px;
  height: 46px;
  background-repeat: no-repeat;
  background-position: center;
}
.review-form__choice[type="radio"]:checked + .review-form__mark {
  background-image: url("../img/radio-mark.svg");
}
.review-form__choice[type="checkbox"]:checked + .review-form__mark {
  background-image: url("../img/checkbox-mark.svg");
}
.review-form__choice:focus + .review-form__mark {
  background-color: #d5d5d5;
}
.review-form__item:hover .review-form__mark {
  background-color: #e5e5e5;
}
.review-form__item:active .review-form__mark {
  background-color: #d5d5d5;
  opacity: 0.3;
}
